:root {
    --content-width: 800px;
}

@mixin container-padding-horizontal {
    padding-right: alley(0.5);
    padding-left: alley(0.5);

    @include min($breakpoint-s) {
        padding-right: alley();
        padding-left: alley();
    }
}

html body #container#container#container {
    @include controlled();
}

