.copyright {
    @include xxsmall();

    text-transform: uppercase;
}

.colorbox {
    width: 42px;
    height: 42px;
    border: 1px solid var(--color-contrast-lower);
    border-radius: 50%;
    box-shadow: 0 0 0 4px #ffffff inset;
}

.decoy {
    min-height: 4rem;
    background: linear-gradient(
        90deg,
        var(--color-contrast-lower) 0%,
        var(--color-contrast-lowest) 100%
    );
}

._404 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .inner {
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -2rem;
        text-align: center;
        transform: translate(-50%, -50%);
    }

    h1 {
        font-size: 8rem;
    }

    p {
        text-transform: uppercase;
        font-size: $font-size-small;
    }
}
